import * as yup from 'yup';
import _isEmpty from 'lodash.isempty';
import { isValidPhoneNumber, parsePhoneNumber } from 'react-phone-number-input';
import _get from 'lodash.get';
import config from '../../../config';

const validateMessage = {
	firstName: 'First Name is required',
	lastName: 'Last Name is required',
	userGroup: 'User Group is required',
	phone: 'Please enter a valid phone number',
};

export const userValidationSchema = yup.object().shape({
	email: yup
		.string()
		.required('Email is required')
		.email('Please enter a valid email address.'),
	firstName: yup.string().required(validateMessage.firstName),
	lastName: yup.string().required(validateMessage.lastName),
	userGroup: yup.string().required(validateMessage.userGroup),
	phone: yup
		.string()
		.test(
			'Phone Number Test',
			'Please enter a valid phone number',
			(value = '') => {
				const phoneNumber = parsePhoneNumber(value, config.locate.countryCode);
				const phone = _get(phoneNumber, 'number');
				return _isEmpty(value) || isValidPhoneNumber(phone || '');
			}
		),
});
